.header-nav {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

  .search-box {
    grid-column-start: 3;
    display: flex;
    z-index: 9999;
    justify-content: flex-end;

    #ajaxsearchlite1 {
      width: 30%;
      height: 65%;
    }
  }

  .menu-shop-container {
    grid-column-start: 3;

    .menu {
      display: flex;

      li {
        padding-right: 10px;
      }
    }
  }
}

.menu li {
  z-index: 9999;
}

a:-webkit-any-link {
  cursor: pointer;
  text-decoration: none;
}

.menu-shop-container {
  text-decoration: none;
}

.header-nav {
  a {
    z-index: 9999;
  }
}

